/* eslint-disable import/default */

// Needed for redux-thunk es6 generator support
import 'babel-polyfill'

import bootstrap from './styles/bootstrap.js'
import React from 'react'
import { render } from 'react-dom'
import { Provider } from 'react-redux'
import { Router, browserHistory } from 'react-router'
import routes from './routes'
import configureStore from './store/configureStore'
import {retrieveLoginInfo} from './actions/accountActions'

require('./favicon.ico') // Tell webpack to load favicon.ico
import './styles/styles.scss' // Yep, that's right. You can import SASS/CSS files too! Webpack will run the associated loader and plug this into the page.
import './styles/image-gallery.css' // Yep, that's right. You can import SASS/CSS files too! Webpack will run the associated loader and plug this into the page.
//import './styles/gsstyles.css'

const store = configureStore();
store.dispatch(retrieveLoginInfo());

render(
  <Provider store={store}>
    <Router history={browserHistory} routes={routes} />
  </Provider>, document.getElementById('app')
);



