'use strict';

/**
 * Config
 */

function getBaseUrl() {    
    return window.location.origin;
}

function getEnvironment() {
    var environment = "";
    switch (window.location.hostname) {
        case "localhost":
            environment = 'DEVELOPMENT';
            break;
        case "surveytab.azurewebsites.net":
            environment = 'TEST';
            break;
        case "fbClone.io":
            environment = 'PROD';
            break;
        case "www.fbClone.io":
            environment = 'PROD';
            break;
        default:
            environment = 'PROD';
            break;
    }
    return environment;
}

function getImgixUrl() {
    var ImgixUrl = "";
    switch (window.location.hostname) {
        case "localhost":
            ImgixUrl = "https://fbClone-test.imgix.net";
            break;
        case "surveytab.azurewebsites.net":
            ImgixUrl = "https://fbClone-test.imgix.net";
            break;
        case "fbClone.io":
            ImgixUrl = "https://fbClone.imgix.net";
            break;
        case "www.fbClone.io":
            ImgixUrl = "https://fbClone.imgix.net";
            break;
        default:
            ImgixUrl = "https://fbClone.imgix.net";
            break;
    }
    return ImgixUrl;
}

function getAlgoliaContainers(container) {
    var containerName = "";
    switch (window.location.hostname) {
        case "localhost":
            containerName = "dev_" + container;
            break;
        case "surveytab.azurewebsites.net":
            containerName = "test_" + container;
            break;
        case "fbClone.io":
            containerName = container;
            break;
        case "www.fbClone.io":
            containerName = container;
            break;
        default:
            containerName = container;
            break;
    }
    return containerName;
}

module.exports = {
    name: 'fbClone',
    version: '1.0.0',
    env: getEnvironment(),
    //mapbox: {
    //    accessToken: process.env.MAPBOX_ACCESS_TOKEN,
    //},
    clientId: 'fbClone',
    //stream: {
    //    appId: 13723,
    //    key: '7n7w23mw7pbv',
    //},
    api: {
        //baseUrl: 'https://localhost:44359',
        //baseUrl: 'http://surveytab.azurewebsites.net',
        //baseUrl: 'https://fbClone.io',
        baseUrl: getBaseUrl()
    },
    imgix: {
        //baseUrl: 'https://fbClone-test.imgix.net',
        //baseUrl: 'https://fbClone.imgix.net',
        baseUrl: getImgixUrl(),
        imageQuality: 30,
        thumbQuality: 30,
    },
    algolia: {
        appId: '8XFQ0K3WAW',
        searchOnlyKey: '145f277ad2c9fb95dade2130f4a47139',
        //userIndex: 'dev_fsUsers',
        //userIndex: 'test_fsUsers',
        //userIndex: 'fsUsers',
        userIndex: getAlgoliaContainers("fsUsers")
    },
    //keen: {
    //    projectId: '57aa1b590727194c4c65eddc',
    //    writeKey: 'f90bbd15305b67c6a6d00e0c3a5d7edc0b7cba3cb96de8e5a6372aaf87da93a2769b2c04935606842494640902b5d6805c22289f90a24e02ba4077c912b787b76d3416b7e9136d0aa11addde263e9c44d597eaffee4964d7e2142f68da8e2984',
    //    readKey: 'b8d07f8e3d110782a084506c5349d0ddc11ea614f2c13f3975ba5dea07fb426f24d4832398f96d29f5054de901d2e5bd4d5569764a383d485fa90f894e01fdcb6931532d59229b9969530606bbbf4fb04ab7c356f7da6ca10571fed3d2b261ac',
    //}
};


